import React from 'react';
import './modal.css'
import photo from './img/photo.png'
class ModalWindow extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return(
            <div className="modal-window">
                <div className="photo-section">
                    {this.props.type === 'blog' ? <BloggerForm /> : <BusinessForm />}
                </div>
            </div>
        );
    }
}
