class LanguageChange extends React.Component {
    constructor (props) {
        super (props);
    }
    render() {
        return (
            <div className="language-change-box default-input">
            <select id={this.props.isMobile ? 'mobile-language' : 'pc-language'} className="language-select">
                <option>EN</option>
                <option>RU</option>
            </select>
            </div>
        );
    }
    
    
}

class BloggerForm extends React.Component {
    constructor (props) {
        super (props);
    }
    render() {
        return(
            <form id="reg-form">
                <div className="photo-and-user-data-box">
                    <ProfilePhoto photo={photo} social={true} />
                    <div className="user-data-field">
                        <input className="default-input top-input" placeholder="Name*" type="text" />
                        <input className="default-input top-input" placeholder="Surname*" type="text" />
                        <div className="gender-form">
                            <div className="gender-label">
                                <input  value="he" name="gender" type="radio" />
                                I - he
                            </div>
                            <div className="gender-label">
                                <input value="she" name="gender" type="radio" />
                                I - she
                            </div>
                        </div>
                        <div className="date-input-label">
                            <p className="gender-text">date of your birth *</p>
                            <div className="data-input-box">
                                <input className="default-input date-input" placeholder="02"/>
                                <input className="default-input date-input" placeholder="26"/>
                                <input className="default-input year-input" placeholder="1999"/>

                            </div>
                        </div>
                        <input className="default-input bottom-input" placeholder="Mail*" type="email"/>
                        <input className="default-input bottom-input" placeholder="Phone*" type="tel"/>
                        <input className="default-input bottom-input" placeholder="Password*" type="password" />
                        <input className="default-input bottom-input" placeholder="Repeat the password*" type="password" />
                    </div>

                </div>
                <div>
                    <select label="Select language*">
                        <option></option>
                    </select>
                    <select label="Country city*">
                        <option></option>

                    </select>
                    <div>
                    <select label="Specialisation*">
                        <option></option>

                    </select>
                    <span>choose up to 3 of your specializations</span>
                    </div>
                    <div>
                        <input type="checkbox" />
                        <span>I accept the <a>terms of the user agreement</a></span>
                    </div>
                </div>
                <button type="submit">Sign up</button>
            </form>  
        );
    }
}

class BusinessForm extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <form id="reg-form">
                <div>
                    <input className="default-input" type="text" placeholder="Name*"/>
                    <input className="default-input" type="text" placeholder="Company name*" />
                    <input className="default-input" type="email" placeholder="Mail*" />
                </div>
                <div>
                    <input className="default-input" type="tel" placeholder="Phone*" />
                    <input className="default-input" placeholder="Phone*" type="tel"/>
                    <input className="default-input" placeholder="Password*" type="password" />
                    <input className="default-input" placeholder="Repeat the password*" type="password" />
                </div>
                <div>
                    <input type="text" placeholder="Your website*" />
                    <select label="Country city*">
                        <option></option>

                    </select>
                    <div>
                        <input className="default-input" type="text" placeholder="Instagram*" />
                        <input className="default-input" type="text" placeholder="facebook*" />
                        <span>please pay attention to write your instagram or facebook page correctly, so bloggers are able to find you.</span>
                    </div>
                    <div>
                        <input type="checkbox" />
                        <span>I accept the <a>terms of the user agreement</a></span>
                    </div>
                </div>
                <button type="submit">Sign up</button>

            </form>
        )
    }
}

export default ModalWindow;