import React from 'react';
import picturesList from './img/svg-list';
import coords from './coords';
import RegWindow from './registration-window';
const BACKEND_URL = 'http://dsde1081-22106.fornex.org'
export function Main() {
    return(
        <main>
            <LoginSection />
        </main>
    );
}

class LoginSection extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentScreen: 'blog',
            renderModal: true,
            modalType: '',
        };
        this.buttonClick = this.buttonClick.bind(this);
        this.openModal = this.openModal.bind(this);
    }

    buttonClick = function(e) {
        const type = e.target.getAttribute('buttontype');
        if(type === this.state.currentScreen) return;
        if(type === 'blog') {
            this.setState({currentScreen:'blog'});
        }
        else {
            this.setState({currentScreen:'business'});
        }
        console.log(this.state.currentScreen, type);
    }
    openModal = function(type) {
        this.setState({renderModal: true, modalType: type});
    }
    render () {
    if (this.state.renderModal) {
        var html = document.getElementsByTagName('html')[0];
        html.style.setProperty("--blur", "20px");
        html.style.setProperty("--pointer", "none");
    }
    else {
        var html = document.getElementsByTagName('html')[0];
        html.style.setProperty("--blur", "0px");
        html.style.setProperty("--pointer", "unset");
    }
    const displayModal = this.state.renderModal ? 'block' : 'none';
    return (
        <>
        <div style={{display: displayModal}} className="overlay">
            {this.state.renderModal ? <RegWindow modalType={this.state.modalType} type={this.state.currentScreen} /> : ''}
        </div>
        <div className="user-type-box" style={{background: `var(--${this.state.currentScreen}-bg-gradient)`}}>
            <div className="user-type-buttons-box">
                <TypeButton buttonType='blog' click={this.buttonClick}isActive={this.state.currentScreen === 'blog'} text='I have a blog'/>
                <TypeButton buttonType='business' click={this.buttonClick}isActive={this.state.currentScreen === 'business'} text='I have a business'/>
            </div>
            <div className={`login-section login-type-${this.state.currentScreen}`}>
                <PictureElement type={this.state.currentScreen} pictures={picturesList[this.state.currentScreen]} delay={3000}/>
                <LoginForm openModal={this.openModal} type={this.state.currentScreen} />
            </div>
            <span className="about-us-text">about us</span>
        </div>
        </>
    );
    }
}
const TypeButton= function(props) {
    const bg = !props.isActive ? '#fff' : 'transparent';
    let classes = "user-type-button-box";
    props.isActive && (classes += " add-corners");
    return(
        <div style={{backgroundColor:bg}} className={classes}>
                <button id={(props.isActive && 'active-btn')||'not-active'} buttontype={props.buttonType} onClick={props.click} className="user-type-button rounded">{props.text}</button>
        </div>
    );
}

class LoginForm extends React.Component {
    constructor(props) {
        super(props);
        this.formSubmit = this.formSubmit.bind(this);
        this.openSignUpModal = this.openSignUpModal.bind(this);
        this.signUpGoogle = this.signUpGoogle.bind(this);
        this.state = {
            initData: null
        }
        
    }
    componentDidMount() {
        if (this.state.initData) return;
        fetch(BACKEND_URL + '/v1/init')
        .then(data => data.json())
        .then(data => {
            console.log(data);
            this.setState({initData: data});
        }).catch(error => error && console.error(error));
    }
    signUpGoogle = function() {
        if (this.state.initData) {
            document.location.href = this.props.type === 'blog' ? this.state.initData["google-signup-blog"]
            :
            this.state.initData["google-signup-busi"]

        }
        else {
            document.location.href = '/'
        }
    }
    openSignUpModal = function() {
        this.props.openModal('reg');
    }
    formSubmit = function() {
        //
    }
    render(){
        return(
        <div className="registration-container">
        <form onSubmit={this.formSubmit} className="registration-form">
            <input className="text-input rounded" type="text" placeholder="Phone number or mail" />
            <input className="text-input rounded" type="password" placeholder="Password" />
            <button className="login-submit-btn rounded" type="submit">Sign in</button>
        </form>
        <a href="#" className="reset-password-link">Forgot your password?</a>
        <button onClick={this.openSignUpModal} className="sign-up-btn rounded">Sign up</button>
        <span className="login-text">or login with</span>
        <div className="social-media-box">
            <button  className="social-media-button">
                <svg width="21" height="22" viewBox="0 0 21 22" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M21 2.75C21 2.14062 20.7656 1.625 20.3438 1.15625C19.875 0.734375 19.3594 0.5 18.75 0.5H2.25C1.59375 0.5 1.07812 0.734375 0.65625 1.15625C0.1875 1.625 0 2.14062 0 2.75V19.25C0 19.9062 0.1875 20.4219 0.65625 20.8438C1.07812 21.3125 1.59375 21.5 2.25 21.5H11.4844V13.2031H8.625V10.0156H11.4844V7.67188C11.4844 6.35938 11.8125 5.375 12.5625 4.625C13.3125 3.875 14.2969 3.5 15.5625 3.5C16.5 3.5 17.3438 3.54688 18 3.64062V6.45312H16.3125C15.7031 6.45312 15.2812 6.59375 15.0469 6.875C14.8594 7.15625 14.7656 7.53125 14.7656 8V10.0156H18L17.5781 13.2031H14.7656V21.5H18.75C19.3594 21.5 19.875 21.3125 20.3438 20.8438C20.7656 20.4219 21 19.9062 21 19.25V2.75Z" fill="white"/>
                </svg>
            </button>
            <button onClick={this.signUpGoogle} className="social-media-button">
                <svg width="23" height="24" viewBox="0 0 23 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M22.875 12.2812C22.875 11.6719 22.7812 11.0156 22.6875 10.3125H11.625V14.3438H18.2344C18.0938 15.0469 17.8125 15.75 17.4375 16.4062C16.875 17.25 16.1719 17.9531 15.3281 18.4219C14.25 19.0781 13.0312 19.3594 11.625 19.3594C10.3125 19.3594 9.09375 19.0312 8.01562 18.375C6.89062 17.7188 6.04688 16.8281 5.39062 15.7031C4.73438 14.5781 4.40625 13.3594 4.40625 12C4.40625 10.4531 4.82812 9.04688 5.67188 7.82812C6.42188 6.70312 7.45312 5.85938 8.76562 5.29688C10.0312 4.73438 11.3438 4.54688 12.6562 4.73438C14.0625 4.92188 15.2344 5.53125 16.2656 6.46875L19.4062 3.42188C17.25 1.40625 14.625 0.375 11.625 0.375C9.51562 0.375 7.59375 0.9375 5.8125 1.96875C4.03125 3 2.57812 4.40625 1.54688 6.1875C0.515625 7.96875 0 9.9375 0 12C0 14.1094 0.515625 16.0312 1.54688 17.8125C2.57812 19.5938 4.03125 21.0469 5.8125 22.0781C7.59375 23.1094 9.51562 23.625 11.625 23.625C13.8281 23.625 15.7969 23.1562 17.4844 22.1719C19.1719 21.2344 20.5312 19.875 21.4688 18.1406C22.4062 16.4531 22.875 14.4844 22.875 12.2812Z" fill="white"/>
                </svg>

            </button>
        </div>
        </div>
        )
    }
}
class PictureElement extends React.Component {
    constructor(props) {
        super(props);
        this.delay = props.delay;
        this.containerRef = React.createRef();
        this.counter = 0;
        this.animate = this.animate.bind(this);
        
    }
    animate = function() {
        const container = this.containerRef.current;
        const text = container.querySelector('.quotes-text');
        const imgs = [...container.querySelectorAll('.picture-container-img')];
        const maxImgIndexValue = imgs.length - 1;
        const previousImgIndex = this.counter - 1 < 0 ? maxImgIndexValue : this.counter - 1;
        const previousImg = imgs[previousImgIndex];
        const currentImg = imgs[this.counter];
        previousImg.style.opacity = '0';
        currentImg.style.opacity = '1';
        this.counter++;
        this.counter > maxImgIndexValue && (this.counter = 0);
        const {left, rigth, top, bottom, rotate} = coords[this.props.type][this.counter];
        const styles = text.style;
        styles.left = left;
        styles.rigth = rigth;
        styles.top = top;
        styles.bottom = bottom;
        styles.opacity = '1';
        styles.transform = `rotate(${rotate}deg)`;
        // const [x, y, angle] = this.coords[this.counter]
        // text.style.transform = `translateX(${x}%) translateY(${y}%) rotate(${angle}deg)`
    }
    componentDidMount() {
        this.animationInterval = setInterval(this.animate, this.delay);
        this.animate();
    }
    componentWillUnmount() {
        clearInterval(this.animationInterval);
    }
    render() {
        return(
            <div className="picture-section" ref={this.containerRef}>
                <div className={`picture-container ${this.props.type}`}>
                    {this.props.pictures.map((el, i) => {
                    const opacity = i === 0 ? 1 : 0;
                    return(
                    <img alt="" className="picture-container-img" key={i} src={el} style={{opacity: opacity}}/>
                    )

                    })
                }
                </div>
                <p className="quotes-text">It will be beneficial to you.<br/>Check it out?</p>
            </div>
        )
    }
}
